import aboutImg from "../assets/bot-1.png";

export default function About() {
    return (
        <div className="container">
            <h1>About</h1>
            <div className="row">
                <img src={aboutImg} alt="" srcset="" />
                <p>
                    One of the things that makes Robotics and Automation Wing unique is the ambition to build dynamically stable, legged and wheeled machines. We've been at it since, almost 13 years. Experiential learning is how we have taught ourselves techniques to make robots work, that one may not find in any of the textbooks. We participate in both national and International events like ABU Robocon, X-Design Challenge, Akruti, E-Yantra and more, over the years we where guided and mentored by Dr. Ravindra Chaudhari, presently the team is lead by Chinmay Pawar. Team RAW was built with a vision of developing task specific robots, daily endeavouring to bridge the gap between Can we do this? to We did this!.


                </p>
            </div>
        </div>
    )
}