import { Container } from "@mui/material";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import { Send } from "@material-ui/icons";
import { useState } from "react";


export default function Contact() {

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [number, setNumber] = useState("");
    const [msg, setMsg] = useState("");

    const contactHandler = () => {
        alert({name}, {email}, {number}, {msg});
    }

    return (
        <Container maxWidth="lg">
            <Grid container spacing={10}>
                <Grid item md={6} xs={12}>
                    <iframe title="Tea Raw" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3766.890206829226!2d72.85365521470027!3d19.243616186992018!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3be7b13affffffff%3A0xfd071f1d3a7844ef!2sSt.%20Francis%20Institute%20of%20Technology!5e0!3m2!1sen!2sin!4v1671512832347!5m2!1sen!2sin" width="600" height="450" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
                </Grid>
                <Grid item md={6} xs={12}>
                    <Stack spacing={3}>
                        <TextField id="name" label="Name" variant="filled" value={name} onChange={(data) => setName(data.value)} />
                        <TextField id="email" label="Email" variant="filled" value={email} onChange={(data) => setEmail(data.value)} />
                        <TextField id="number" label="Number" variant="filled" value={number} onChange={(data) => setNumber(data.value)} />
                        <TextField id="message" label="Message" variant="filled" multiline rows={4} value={msg} onChange={(data) => setMsg(data.value)} />
                        <Button variant="outlined" onClick={contactHandler} startIcon={<Send />}>
                            Send
                        </Button>
                    </Stack>
                </Grid>
            </Grid>
        </Container>
    )
}